import {HTTPParser, HttpMethods} from './http-parser'
import OutgoingMessage from './http_outgoing'
import * as HttpWsError from './errors'
import * as Common from './common'

const debug = Common.debug;
const CRLF = Common.CRLF;
const continueExpression = Common.continueExpression;
const chunkExpression = Common.chunkExpression;
const httpSocketSetup = Common.httpSocketSetup;
const STATUS_CODES =Common.STATUS_CODES;
const outHeadersKey = Common.outHeadersKey;

const kOnExecute = HTTPParser.kOnExecute | 0;

export default class ServerResponse extends OutgoingMessage{
    constructor(command,req) {
        super();
        this.command = command;

        if (!req) return;
        if (req.method === 'HEAD') this._hasBody = false;

        this.sendDate = true;
        this._sent100 = false;
        this._expect_continue = false;
        this.code = 200;
        this.message = undefined;

        if (req.httpVersionMajor < 1 || req.httpVersionMinor < 1) {
            this.useChunkedEncodingByDefault = chunkExpression.test(req.headers.te);
            this.shouldKeepAlive = false;
        }
    }

    get statusCode() {
        return this.code;
    }

    set statusCode(val) {
        this.code = val;
    }

    get statusMessage() {
        return this.message;
    }

    set statusMessage(val) {
        this.message = val;
    }

    writeContinue(cb) {
        this._writeRaw('HTTP/1.1 100 Continue' + CRLF + CRLF, 'ascii', cb);
        this._sent100 = true;
    }

    _implicitHeader() {
        this.writeHead(this.statusCode);
    }

    writeHead(statusCode, reason, obj) {
        var originalStatusCode = statusCode;

        statusCode |= 0;
        if (statusCode < 100 || statusCode > 999) {
            throw new HttpWsError.RangeError('ERR_HTTP_INVALID_STATUS_CODE',
                originalStatusCode);
        }


        if (typeof reason === 'string') {
            // writeHead(statusCode, reasonPhrase[, headers])
            this.statusMessage = reason;
        } else {
            // writeHead(statusCode[, headers])
            if (!this.statusMessage)
                this.statusMessage = STATUS_CODES[statusCode] || 'unknown';
            obj = reason;
        }
        this.statusCode = statusCode;

        var headers;
        if (this[outHeadersKey]) {
            // Slow-case: when progressive API and header fields are passed.
            var k;
            if (obj) {
                var keys = Object.keys(obj);
                for (var i = 0; i < keys.length; i++) {
                    k = keys[i];
                    if (k) this.setHeader(k, obj[k]);
                }
            }
            if (k === undefined && this._header) {
                throw new HttpWsError.Error('ERR_HTTP_HEADERS_SENT', 'render');
            }
            // only progressive api is used
            headers = this[outHeadersKey];
        } else {
            // only writeHead() called
            headers = obj;
        }

        if (common._checkInvalidHeaderChar(this.statusMessage))
            throw new HttpWsError.Error('ERR_INVALID_CHAR', 'statusMessage');

        var statusLine = 'HTTP/1.1 ' + statusCode + ' ' + this.statusMessage + CRLF;

        if (statusCode === 204 || statusCode === 304 ||
            (statusCode >= 100 && statusCode <= 199)) {
            // RFC 2616, 10.2.5:
            // The 204 response MUST NOT include a message-body, and thus is always
            // terminated by the first empty line after the header fields.
            // RFC 2616, 10.3.5:
            // The 304 response MUST NOT contain a message-body, and thus is always
            // terminated by the first empty line after the header fields.
            // RFC 2616, 10.1 Informational 1xx:
            // This class of status code indicates a provisional response,
            // consisting only of the Status-Line and optional headers, and is
            // terminated by an empty line.
            this._hasBody = false;
        }

        // don't keep alive connections where the client expects 100 Continue
        // but we sent a final status; they may put extra bytes on the wire.
        if (this._expect_continue && !this._sent100) {
            this.shouldKeepAlive = false;
        }

        this._storeHeader(statusLine, headers);
    }
}



