import Command from './command'
import {debug} from './common'
import {HTTPParser} from './http-parser'

export default class ClientCommand extends Command {
    constructor(connection, id) {
        super(connection, id, HTTPParser.RESPONSE);
        let n = this.app.maxHeadersCount;
        // Propagate headers limit from server instance to parser
        if (n && typeof n === 'number') {
            this._parser.maxHeaderPairs = n << 1;
        } else {
            // Set default value because parser may be reused from FreeList
            this._parser.maxHeaderPairs = 2000;
        }
        this._id = id;
        this._client_request = null;

    }
    setFlush(request) {
        this._client_request = request;
        let req = this._client_request;
        let cmd = this;
        req.on('write', function (data, encoding, callback) {
            cmd.write(data, encoding);
            if (callback) {
                callback();
            }
        });
    }
    _parserOnIncoming(res, shouldKeepAlive) {
        let req = this._client_request;
        if (req === null) {
            debug('No request corresponded to the response : CSeq ', this._id);
            return -1;
        }
        req.clearTimeout();
        req.emit('response', res);

        return 0;
    }
}



