import ServiceCommand from './service_command'
import ClientCommand from './client_command'
import EventEmitter from './EventEmitter';
import {debug, continueExpression} from './common'
import ClientRequest from './client_request'

class Connection extends EventEmitter {
    constructor(socket, app) {
        super();
        let self = this;
        this.on('chunk', (chunk) => {
            let m = _parse(chunk);
            if (m) {
                self['on_incoming_' + m.type](m.data, m.id, m.last);
            }
        });
        this.on('finish', (id) => {
            delete this.scommand[id];
        });

        this.on('client_request', (request, id) => {
            let cmd = this.ccommand[id];
            if (!cmd) {
                this.ccommand[id] = new ClientCommand(this, id);
                cmd = this.ccommand[id];
                cmd.maxHeaderPairs = this.maxHeaderPairs;
                cmd.setFlush(request);
                var self = this;

                request.on('timeout_notify', () => {
                    debug('client request timeout, id: ' + id);
                    request.emit('timeout');
                    delete self.ccommand[id];
                });
            }
        });

        this.socket = socket;
        this.app = app;
        this.scommand = {}; //service command
        this.ccommand = {}; //client command

        // Propagate headers limit from server instance to parser
        if (typeof app.maxHeadersCount === 'number') {
            this.maxHeaderPairs = app.maxHeadersCount << 1;
        } else {
            // Set default value because parser may be reused from FreeList
            this.maxHeaderPairs = 2000;
        }

    }

    flushOut(data, encoding) {
        socket.send(chunk);
    }

    on_incoming_request(data, id, last) {
        let cmd = this.scommand[id];
        if (!cmd) {
            cmd = new ServiceCommand(this, id);
            this.scommand[id] = cmd;
            cmd.maxHeaderPairs = this.maxHeaderPairs;

            var self = this;
            cmd.on('timeout_notify', () => {
                debug('service command timeout, id: ' + id);
                delete self.scommand[id];
            });
            cmd.on('finish', () => {
                delete self.scommand[id];
            });
        }
        cmd.emit('chunk', data, last);
    }

    on_incoming_response(data, id, last) {
        let cmd = this.ccommand[id];
        if (cmd) {
            cmd.emit('chunk', data, last);
        }
    }
}

function _parse(chunk) {
    const _CONLON = 0x3A; // :
    const _CR = 0x0D; //\r
    const _LF = 0x0A; //\n

    if (!chunk || chunk[0] !== _CONLON) {
        debug('Invalide message');
        return;
    }

    let n = chunk.indexOf(_CR, 1);
    if (n === -1)
        return;
    let line = chunk.toString('ascii', 1, n);

    let pattern = /(request|response|message)\s+([0-9]+)\s*([0-9]+)?/;
    let arr = pattern.exec(line)
    if (arr === null) {
        debug('Invalid command line:', line);
        return;
    }
    let type = arr[1];
    let id = Number(arr[2]);
    let last = arr[3] === undefined ? true : false;
    let data = chunk.slice(n + 2);
    return {
        type,
        id,
        last,
        data
    };
}

var sconn_list = {};
var cconn_list = {};

function getConnection(name, arg) {
    if (arg === undefined) {
        return cconn_list[name];
    } else {
        return sconn_list[name];
    }
}

function addConnection(name, conn, arg) {
    if (arg === undefined) {
        if (cconn_list[name]) {
            return;
        }
        cconn_list[name] = conn;
    } else {
        if (sconn_list[name]) {
            return;
        }
        sconn_list[name] = conn;
    }
}

function removeConnection(name, arg) {
    if (arg === undefined) {
        delete cconn_list[name];
    } else {
        delete sconn_list[name];
    }
}

var _request_counter = 0;

function request(options, cb, arg) {
    var client_request = new ClientRequest(options, cb);
    var name = client_request.getHeader('host');
    var id = client_request.getHeader('CSeq');
    var conn = getConnection(name, arg);
    if (conn === undefined || conn === null) {
        throw new Error('The connection hasn\'t been established with: ' + name);
    }

    conn.emit('client_request', client_request, id);
    return client_request;
}

export {
    Connection,
    getConnection,
    addConnection,
    removeConnection,
    request
}



