import {Buffer} from 'Buffer'

const MAX_SIZE_COMMAND_LINE = 64;
export default class OBuf {
	constructor(prefix, socket) {

		var n = Math.max(Buffer.poolSize / 2, 4096);
		this._buf = Buffer.alloc(n);
		this._wrpos = MAX_SIZE_COMMAND_LINE;
		// this._endpos = n;
		// this._type = type;
		// this._Id = id;
		this._chunkId = 0;
		this._finish = false;
		this._prefix = prefix; //':'+type + ' ' + id;
		this._socket = socket;
	}

	wrable_size() {
		return this._buf.length > this._wrpos ? (this._buf.length - this._wrpos) : 0;
	}

	append(data, encoding) {
		if (data === undefined && encoding === undefined) {
			this._finish = true;
			this.flush();
			return;
		}
		// data = Buffer.from(data, encoding);
		if (typeof data === 'string') {
			var n = Buffer.byteLength(data, encoding);
			if (n <= this.wrable_size()) {
				this._wrpos += this._buf.write(data, this._wrpos, encoding);
				if (this.wrable_size() == 0) {
					this.flush();
				}
				return;
			} else {
				if (this.wrable_size() == 0) {
					this.flush();
				}
				if (n > this.wrable_size()) {
					this.flush(Buffer(data, encoding)); //max block send directly
				}
			}
			// data = Buffer.from(data, encoding);
		}
		else {
			if (data.length <= this.wrable_size()) {
				data.copy(this._buf, this._wrpos);
				this._wrpos += data.length;
			} else {
				//slice
				while (data.length > this.wrable_size()) {
					var len = this.wrable_size();
					data.copy(this._buf, this._wrpos, 0, len);
					this._wrpos += len;
					data = data.slice(len);
					this.flush();
				}
				data.copy(this._buf, this._wrpos);
				this._wrpos += data.length;
			}
		 }
	}

	flush(buf) {
		var chunkid = '';
		if (!this._finish) {
			this._chunkId++;
		}

		if (this._chunkId) {
			chunkid = ' ' + this._chunkId;
		}
		var cline;
		if (!this._finish) {
			cline = this._prefix + chunkid + '\r\n';
		} else {
			cline = this._prefix + '\r\n';
		}
		if (buf) {
			var b = Buffer.concat([Buffer(cline), buf]);
			this._socket.send(b);
			// console.log('-------------')
			// console.log(b.toString());
			// console.log('-------------')
		} else {
			var offset = MAX_SIZE_COMMAND_LINE - cline.length;
			this._buf.write(cline, offset, 'ascii');
			this._socket.send(this._buf.slice(offset, this._wrpos));
			// console.log('-------------')
			// console.log(this._buf.slice(offset, this._wrpos).toString());
			// console.log('-------------')
			this._buf.fill(0);
			this._wrpos = MAX_SIZE_COMMAND_LINE;
		}
	}

}



