import url from 'url'
import * as Common from './common'
import OutgoingMessage from './http_outgoing'
import util from 'util'
import {Base64} from 'js-base64'
import * as HttpWsError from './errors'

const debug = Common.debug;
const outHeadersKey = Common.outHeadersKey;

var _request_counter = 0;

// The actual list of disallowed characters in regexp form is more like:
//    /[^A-Za-z0-9\-._~!$&'()*+,;=/:@]/
// with an additional rule for ignoring percentage-escaped characters, but
// that's a) hard to capture in a regular expression that performs well, and
// b) possibly too restrictive for real-world usage. So instead we restrict the
// filter to just control characters and spaces.
//
// This function is used in the case of small paths, where manual character code
// checks can greatly outperform the equivalent regexp (tested in V8 5.4).
function isInvalidPath(s) {
    let i = 0;
    if (s.charCodeAt(0) <= 32) return true;
    if (++i >= s.length) return false;
    if (s.charCodeAt(1) <= 32) return true;
    if (++i >= s.length) return false;
    if (s.charCodeAt(2) <= 32) return true;
    if (++i >= s.length) return false;
    if (s.charCodeAt(3) <= 32) return true;
    if (++i >= s.length) return false;
    if (s.charCodeAt(4) <= 32) return true;
    if (++i >= s.length) return false;
    if (s.charCodeAt(5) <= 32) return true;
    ++i;
    for (; i < s.length; ++i)
        if (s.charCodeAt(i) <= 32) return true;
    return false;
}

function validateHost(host, name) {
    if (host !== null && typeof host !== 'string') {
        throw new HttpWsError.TypeError('ERR_INVALID_ARG_TYPE', `options.${name}`, ['string', 'undefined', 'null'], host);
    }
    return host;
}

function url2options(Url) {
    //find virtual hostname {xxxx}
    let line = Url;
    let xpath = null;
    let begin = line.indexOf('://{');
    if (begin !== -1) {
        let end = line.indexOf('}');
        if (end === -1) {
            debug('invalid url(without }end for hostname) :', line);
            return null;
        }
        xpath = line.slice(begin + 4, end);
        line = line.slice(0, begin + 3) + '.' + line.slice(end + 1);
    }

    let options = url.parse(line);

    if (xpath) {
        options.host = xpath;
        options.hostname = null;
        let href = options.href;
        if (href) {
            let n = href.indexOf('://./');
            if (n !== -1) {
                options.href = href.slice(0, n + 3) + `{${xpath}}` + href.slice(n + 4);
            }
        }
    } else {
        options.host = options.hostname
    }
    return options;
}

export default class ClientRequest extends OutgoingMessage {
    constructor(options, cb) {
        super();

        if (typeof options === 'string') {
            options = url2options(options);
            if (options.hostname !== null && !options.hostname) {
                throw new Error('ERR_INVALID_DOMAIN_NAME');
            }
        } else {
            options = util._extend({}, options);
            if (!options.host) {
                options.host = options.hostname;
            }
        }

        let protocol = options.protocol || 'http:';
        let expectedProtocol = 'http:';

        let path;
        if (options.path) {
            path = '' + options.path;
            let invalidPath;
            if (path.length <= 39) { // Determined experimentally in V8 5.4
                invalidPath = isInvalidPath(path);
            } else {
                invalidPath = /[\u0000-\u0020]/.test(path);
            }
            if (invalidPath)
                throw new HttpWsError.TypeError('ERR_UNESCAPED_CHARACTERS', 'Request path');
        }

        if (protocol !== expectedProtocol) {
            throw new Error('ERR_INVALID_PROTOCOL', protocol, expectedProtocol);
        }

        let defaultPort = 80;
        let port = options.port = options.port || defaultPort;
        let host = options.host = validateHost(options.host, 'host') || validateHost(options.hostname, 'hostname') || 'localhost';


        let setHost = (options.setHost === undefined);

        this.timeout = options.timeout ? options.timeout : 2 * 60 * 1000;

        let method = options.method;
        let methodIsString = (typeof method === 'string');
        if (method !== null && !methodIsString) {
            throw new Error('ERR_INVALID_ARG_TYPE', 'method',
                'string', method);
        }

        if (methodIsString && method) {
            if (!Common._checkIsHttpToken(method)) {
                throw new Error('ERR_INVALID_HTTP_TOKEN', 'Method');
            }
            method = this.method = method.toUpperCase();
        } else {
            method = this.method = 'GET';
        }

        this.path = options.path || '/';
        if (cb) {
            this.once('response', cb);
        }

        let headersArray = Array.isArray(options.headers);
        if (!headersArray) {
            if (options.headers) {
                let keys = Object.keys(options.headers);
                for (let i = 0; i < keys.length; i++) {
                    let key = keys[i];
                    this.setHeader(key, options.headers[key]);
                }
            }

            if (host && !this.getHeader('host') && setHost) {
                let hostHeader = host;

                // For the Host header, ensure that IPv6 addresses are enclosed
                // in square brackets, as defined by URI formatting
                // https://tools.ietf.org/html/rfc3986#section-3.2.2
                let posColon = hostHeader.indexOf(':');
                if (posColon !== -1 &&
                    hostHeader.indexOf(':', posColon + 1) !== -1 &&
                    hostHeader.charCodeAt(0) !== 91 /*'['*/ ) {
                    hostHeader = `[${hostHeader}]`;
                }

                if (port && +port !== defaultPort) {
                    hostHeader += ':' + port;
                }
                this.setHeader('Host', hostHeader);
                // console.log('%%', hostHeader)
            }
        }

        if (options.auth && !this.getHeader('Authorization')) {
            this.setHeader('Authorization', 'Basic ' + Base64.encode(options.auth));
        }
        /*
          if (method === 'GET' ||
              method === 'HEAD' ||
              method === 'DELETE' ||
              method === 'OPTIONS' ||
              method === 'CONNECT') {
            this.useChunkedEncodingByDefault = false;
          } else {
            this.useChunkedEncodingByDefault = true;
          }
        */
        this.useChunkedEncodingByDefault = false;

        if (headersArray) {
            this._storeHeader(this.method + ' ' + this.path + ' HTTP/1.1\r\n',
                options.headers);
        } else if (this.getHeader('expect')) {
            if (this._header) {
                throw new Error('ERR_HTTP_HEADERS_SENT');
            }

            this._storeHeader(this.method + ' ' + this.path + ' HTTP/1.1\r\n',
                this[outHeadersKey]);
        }

        this._ended = false;
        this.res = null;
        this.aborted = undefined;
        this.timeoutCb = null;
        this.upgradeOrConnect = false;
        this.parser = null;
        this.maxHeadersCount = null;

        this._obuf = null;
        var id = ++_request_counter ? _request_counter : _request_counter = 1;
        this.setHeader('CSeq', id);
        let called = false;

        //this.once('release', this.release);
        let req = this;
        this.timeout_handler = setTimeout(function () {
            req.emit('timeout_notify');
        }, this.timeout);
    }

    _finish() {
        DTRACE_HTTP_CLIENT_REQUEST(this, this.connection);
        LTTNG_HTTP_CLIENT_REQUEST(this, this.connection);
        COUNTER_HTTP_CLIENT_REQUEST();
        OutgoingMessage.prototype._finish.call(this);
    }

    _implicitHeader() {
        if (this._header) {
            throw new HttpWsError.Error('ERR_HTTP_HEADERS_SENT', 'render');
        }
        this._storeHeader(this.method + ' ' + this.path + ' HTTP/1.1\r\n',
            this[outHeadersKey]);
    }

    release() {
        globalAgent.RemoveRequestByReq(this);
        this.emit('timeout_notify');
    }

    close() {
    }

    clearTimeout() {
        if (this.timeout_handler != null) {
            clearTimeout(this.timeout_handler);
        }
    }
}



