import EventEmitter from './EventEmitter'
import * as Common from './common'
import OBuf from './obuf'
import * as HttpWsError from './errors'
import {Buffer} from 'Buffer'

const checkIsHttpToken = Common._checkIsHttpToken;
const checkInvalidHeaderChar = Common._checkInvalidHeaderChar;
const outHeadersKey = Common.outHeadersKey;
const CRLF = Common.CRLF;
const debug = Common.debug;
const utcDate = Common.utcDate;

var RE_FIELDS =
    /^(?:Connection|Transfer-Encoding|Content-Length|Date|Expect|Trailer|Upgrade)$/i;
var RE_CONN_VALUES = /(?:^|\W)close|upgrade(?:$|\W)/ig;
var RE_TE_CHUNKED = Common.chunkExpression;

// isCookieField performs a case-insensitive comparison of a provided string
// against the word "cookie." This method (at least as of V8 5.4) is faster than
// the equivalent case-insensitive regexp, even if isCookieField does not get
// inlined.
function isCookieField(s) {
    if (s.length !== 6) return false;
    var ch = s.charCodeAt(0);
    if (ch !== 99 && ch !== 67) return false;
    ch = s.charCodeAt(1);
    if (ch !== 111 && ch !== 79) return false;
    ch = s.charCodeAt(2);
    if (ch !== 111 && ch !== 79) return false;
    ch = s.charCodeAt(3);
    if (ch !== 107 && ch !== 75) return false;
    ch = s.charCodeAt(4);
    if (ch !== 105 && ch !== 73) return false;
    ch = s.charCodeAt(5);
    if (ch !== 101 && ch !== 69) return false;
    return true;
}

function noopPendingOutput(amount) {}

const MAX_SIZE_COMMAND_LINE =64;

export default class OutgoingMessage extends EventEmitter {
    constructor() {
        super();
        // Queue that holds all currently pending data, until the response will be
        // assigned to the socket (until it will its turn in the HTTP pipeline).
        this.output = [];
        this.outputEncodings = [];
        this.outputCallbacks = [];

        // `outputSize` is an approximate measure of how much data is queued on this
        // response. `_onPendingData` will be invoked to update similar global
        // per-connection counter. That counter will be used to pause/unpause the
        // TCP socket and HTTP Parser and thus handle the backpressure.
        this.outputSize = 0;

        this.writable = true;

        this._last = false;
        this.upgrading = false;
        this.chunkedEncoding = false;
        this.shouldKeepAlive = true;
        this.useChunkedEncodingByDefault = true;
        this.sendDate = false;
        this._removedConnection = false;
        this._removedContLen = false;
        this._removedTE = false;

        this._contentLength = null;
        this._hasBody = true;
        this._trailer = '';

        this.finished = false;
        this._headerSent = false;

//  this.socket = null;
//  this.connection = null;
        this._header = null;
        this[outHeadersKey] = null;

        this._onPendingData = noopPendingOutput;

        this._closed = false; //indicate the connection socket closed
    }

    _renderHeaders() {
        if (this._header) {
            throw new HttpWsError.Error('ERR_HTTP_HEADERS_SENT', 'render');
        }

        var headersMap = this[outHeadersKey];
        if (!headersMap) return {};

        var headers = {};
        var keys = Object.keys(headersMap);

        for (var i = 0, l = keys.length; i < l; i++) {
            var key = keys[i];
            headers[headersMap[key][0]] = headersMap[key][1];
        }
        return headers;
    }

    close() {
        this._closed = true;
        this.emit('close');
    }

    _send(data, encoding, callback) {
        // This is a shameful hack to get the headers and first body chunk onto
        // the same packet. Future versions of Node are going to take care of
        // this at a lower level and in a more general way.
        if (!this._headerSent) {
            if (typeof data === 'string' &&
                (encoding === 'utf8' || encoding === 'latin1' || !encoding)) {
                data = this._header + data;
            } else {
                var header = this._header;
                if (this.output.length === 0) {
                    this.output = [header];
                    this.outputEncodings = ['latin1'];
                    this.outputCallbacks = [null];
                } else {
                    this.output.unshift(header);
                    this.outputEncodings.unshift('latin1');
                    this.outputCallbacks.unshift(null);
                }
                this.outputSize += header.length;
                this._onPendingData(header.length);
            }
            this._headerSent = true;
        }
        return this._writeRaw(data, encoding, callback);
    }

    _writeRaw(data, encoding, callback) {
        //const conn = this.connection;
        //do nothing once socket closed
        if ( this._closed /*conn && conn.destroyed*/) {
            // The socket was destroyed. If we're still trying to write to it,
            // then we haven't gotten the 'close' event yet.
            return false;
        }

        if (typeof encoding === 'function') {
            callback = encoding;
            encoding = null;
        }

        //if (conn && conn._httpMessage === this && conn.writable && !conn.destroyed) {
        if( ! this._closed ){
            // There might be pending data in the this.output buffer.
            if (this.output.length) {
                this._flushOutput();
            }
            // else if (!data.length) {
            //   if (typeof callback === 'function') {
            //     nextTick(/*this.socket[async_id_symbol],*/ callback);
            //   }
            //   // return true;
            // }
            // Directly write to socket.
            //return conn.write(data, encoding, callback);
            this.emit('write',data, encoding, callback)
            return true;
        }
        // Buffer, as long as we're not destroyed.
        this.output.push(data);
        this.outputEncodings.push(encoding);
        this.outputCallbacks.push(callback);
        this.outputSize += data.length;
        this._onPendingData(data.length);
        return false;
    }

    _storeHeader(firstLine, headers) {
        // firstLine in the case of request is: 'GET /index.html HTTP/1.1\r\n'
        // in the case of response it is: 'HTTP/1.1 200 OK\r\n'
        var state = {
            connection: false,
            connUpgrade: false,
            contLen: false,
            te: false,
            date: false,
            expect: false,
            trailer: false,
            upgrade: false,
            header: firstLine
        };

        var field;
        var key;
        var value;
        var i;
        var j;
        if (headers === this[outHeadersKey]) {
            for (key in headers) {
                var entry = headers[key];
                field = entry[0];
                value = entry[1];

                if (value instanceof Array) {
                    if (value.length < 2 || !isCookieField(field)) {
                        for (j = 0; j < value.length; j++)
                            storeHeader(this, state, field, value[j], false);
                        continue;
                    }
                    value = value.join('; ');
                }
                storeHeader(this, state, field, value, false);
            }
        } else if (headers instanceof Array) {
            for (i = 0; i < headers.length; i++) {
                field = headers[i][0];
                value = headers[i][1];

                if (value instanceof Array) {
                    for (j = 0; j < value.length; j++) {
                        storeHeader(this, state, field, value[j], true);
                    }
                } else {
                    storeHeader(this, state, field, value, true);
                }
            }
        } else if (headers) {
            var keys = Object.keys(headers);
            for (i = 0; i < keys.length; i++) {
                field = keys[i];
                value = headers[field];

                if (value instanceof Array) {
                    if (value.length < 2 || !isCookieField(field)) {
                        for (j = 0; j < value.length; j++)
                            storeHeader(this, state, field, value[j], true);
                        continue;
                    }
                    value = value.join('; ');
                }
                storeHeader(this, state, field, value, true);
            }
        }

        // Are we upgrading the connection?
        if (state.connUpgrade && state.upgrade)
            this.upgrading = true;

        // Date header
        if (this.sendDate && !state.date) {
            state.header += 'Date: ' + utcDate() + CRLF;
        }

        // Force the connection to close when the response is a 204 No Content or
        // a 304 Not Modified and the user has set a "Transfer-Encoding: chunked"
        // header.
        //
        // RFC 2616 mandates that 204 and 304 responses MUST NOT have a body but
        // node.js used to send out a zero chunk anyway to accommodate clients
        // that don't have special handling for those responses.
        //
        // It was pointed out that this might confuse reverse proxies to the point
        // of creating security liabilities, so suppress the zero chunk and force
        // the connection to close.
        var statusCode = this.statusCode;
        if ((statusCode === 204 || statusCode === 304) && this.chunkedEncoding) {
            debug(statusCode + ' response should not use chunked encoding,' +
                ' closing connection.');
            this.chunkedEncoding = false;
            this.shouldKeepAlive = false;
        }

        // keep-alive logic
        if (this._removedConnection) {
            this._last = true;
            this.shouldKeepAlive = false;
        } else if (!state.connection) {
            var shouldSendKeepAlive = this.shouldKeepAlive &&
                (state.contLen || this.useChunkedEncodingByDefault || this.agent);
            if (shouldSendKeepAlive) {
                state.header += 'Connection: keep-alive\r\n';
            } else {
                this._last = true;
                state.header += 'Connection: close\r\n';
            }
        }

        if (!state.contLen && !state.te) {
            if (!this._hasBody) {
                // Make sure we don't end the 0\r\n\r\n at the end of the message.
                this.chunkedEncoding = false;
            } else if (!this.useChunkedEncodingByDefault) {
                this._last = true;
            } else {
                if (!state.trailer &&
                    !this._removedContLen &&
                    typeof this._contentLength === 'number') {
                    state.header += 'Content-Length: ' + this._contentLength + CRLF;
                } else if (!this._removedTE) {
                    state.header += 'Transfer-Encoding: chunked\r\n';
                    this.chunkedEncoding = true;
                } else {
                    // We should only be able to get here if both Content-Length and
                    // Transfer-Encoding are removed by the user.
                    // See: test/parallel/test-http-remove-header-stays-removed.js
                    debug('Both Content-Length and Transfer-Encoding are removed');
                }
            }
        }

        // Test non-chunked message does not have trailer header set,
        // message will be terminated by the first empty line after the
        // header fields, regardless of the header fields present in the
        // message, and thus cannot contain a message body or 'trailers'.
        if (this.chunkedEncoding !== true && state.trailer) {
            throw new HttpWsError.Error('ERR_HTTP_TRAILER_INVALID');
        }

        this._header = state.header + CRLF;
        this._headerSent = false;

        // wait until the first body chunk, or close(), is sent to flush,
        // UNLESS we're sending Expect: 100-continue.
        if (state.expect) this._send('');
    }

    setHeader(name, value) {
        validateHeader(this, name, value);

        if (!this[outHeadersKey])
            this[outHeadersKey] = {};

        const key = name.toLowerCase();
        this[outHeadersKey][key] = [name, value];

        switch (key.length) {
            case 10:
                if (key === 'connection')
                    this._removedConnection = false;
                break;
            case 14:
                if (key === 'content-length')
                    this._removedContLen = false;
                break;
            case 17:
                if (key === 'transfer-encoding')
                    this._removedTE = false;
                break;
        }
    }

    getHeader(name) {
        if (typeof name !== 'string') {
            throw new HttpWsError.TypeError('ERR_INVALID_ARG_TYPE', 'name', 'string');
        }

        if (!this[outHeadersKey]) return;

        var entry = this[outHeadersKey][name.toLowerCase()];
        if (!entry)
            return;
        return entry[1];
    }

    getHeaderNames() {
        return (this[outHeadersKey] ? Object.keys(this[outHeadersKey]) : []);
    }

    getHeaders() {
        const headers = this[outHeadersKey];
        const ret = Object.create(null);
        if (headers) {
            const keys = Object.keys(headers);
            for (let i = 0; i < keys.length; ++i) {
                const key = keys[i];
                const val = headers[key][1];
                ret[key] = val;
            }
        }
        return ret;
    }

    hasHeader(name) {
        if (typeof name !== 'string') {
            throw new HttpWsError.TypeError('ERR_INVALID_ARG_TYPE', 'name', 'string');
        }

        return !!(this[outHeadersKey] && this[outHeadersKey][name.toLowerCase()]);
    }

    removeHeader(name) {
        if (typeof name !== 'string') {
            throw new HttpWsError.TypeError('ERR_INVALID_ARG_TYPE', 'name', 'string');
        }

        if (this._header) {
            throw new HttpWsError.Error('ERR_HTTP_HEADERS_SENT', 'remove');
        }

        let key = name.toLowerCase();

        switch (key.length) {
            case 10:
                if (key === 'connection')
                    this._removedConnection = true;
                break;
            case 14:
                if (key === 'content-length')
                    this._removedContLen = true;
                break;
            case 17:
                if (key === 'transfer-encoding')
                    this._removedTE = true;
                break;
            case 4:
                if (key === 'date')
                    this.sendDate = false;
                break;
        }

        if (this[outHeadersKey]) {
            delete this[outHeadersKey][key];
        }
    }

    _implicitHeader() {
        throw new HttpWsError.Error('ERR_METHOD_NOT_IMPLEMENTED', '_implicitHeader()');
    }

    write(chunk, encoding, callback) {
        return write_(this, chunk, encoding, callback, false);
    }

    addTrailers(headers) {
        this._trailer = '';
        var keys = Object.keys(headers);
        var isArray = Array.isArray(headers);
        var field, value;
        for (var i = 0, l = keys.length; i < l; i++) {
            var key = keys[i];
            if (isArray) {
                field = headers[key][0];
                value = headers[key][1];
            } else {
                field = key;
                value = headers[key];
            }
            if (typeof field !== 'string' || !field || !checkIsHttpToken(field)) {
                throw new HttpWsError.TypeError('ERR_INVALID_HTTP_TOKEN', 'Trailer name',
                    field);
            }
            if (checkInvalidHeaderChar(value)) {
                debug('Trailer "%s" contains invalid characters', field);
                throw new HttpWsError.TypeError('ERR_INVALID_CHAR', 'trailer content', field);
            }
            this._trailer += field + ': ' + escapeHeaderValue(value) + CRLF;
        }
    }

    end(chunk, encoding, callback) {
        if (typeof chunk === 'function') {
            callback = chunk;
            chunk = null;
        } else if (typeof encoding === 'function') {
            callback = encoding;
            encoding = null;
        }

        if (this.finished) {
            return false;
        }

//  var uncork;
        if (chunk) {
            if (typeof chunk !== 'string' && !(chunk instanceof Buffer)) {
                throw new HttpWsError.TypeError('ERR_INVALID_ARG_TYPE', 'first argument',
                    ['string', 'buffer']);
            }
            if (!this._header) {
                if (typeof chunk === 'string')
                    this._contentLength = Buffer.byteLength(chunk, encoding);
                else
                    this._contentLength = chunk.length;
            }
//    if (this.connection) {
//      this.connection.cork();
//      uncork = true;
//    }
            write_(this, chunk, encoding, null, true);
        } else if (!this._header) {
            this._contentLength = 0;
            this._implicitHeader();
        }

        if (typeof callback === 'function')
            this.once('finish', callback);

        var finish = onFinish.bind(undefined, this);

        var ret;
        /*
          if (this._hasBody && this.chunkedEncoding) {
            ret = this._send('0\r\n' + this._trailer + '\r\n', 'latin1', finish);
          } else {
            // Force a flush, HACK.
            ret = this._send('', 'latin1', finish);
          }
        */
        ret = this._send();
//  if (uncork)
//    this.connection.uncork();

        this.finished = true;
        this.writable = false;

        // There is the first message on the outgoing queue, and we've sent
        // everything to the socket.
        debug('outgoing message end.');
//  if (this.output.length === 0 &&
//      this.connection &&
//      this.connection._httpMessage === this) {
//    this._finish();
//  }
        this.close();
        return ret;
    }

    _finish() {
        //assert(this.connection);
        this.emit('prefinish');
    }

    _flushOutput(socket) {
        var ret;
        var outputLength = this.output.length;
        if (outputLength <= 0)
            return ret;

        var output = this.output;
        var outputEncodings = this.outputEncodings;
        var outputCallbacks = this.outputCallbacks;
        //socket.cork();
        for (var i = 0; i < outputLength; i++) {
            //ret = socket.write(output[i], outputEncodings[i], outputCallbacks[i]);
            ret = this.emit('flush',output[i], outputEncodings[i], outputCallbacks[i]);
        }
        //socket.uncork();

        this.output = [];
        this.outputEncodings = [];
        this.outputCallbacks = [];
        this._onPendingData(-this.outputSize);
        this.outputSize = 0;

        return ret;
    }

    flushHeaders() {
        if (!this._header) {
            this._implicitHeader();
        }

        // Force-flush the headers.
        this._send('');
    }

    _flushOut(data,encoding){
        if (this._obuf == null) {
            var prefix;
            if (this._type = HTTPParser.REQUEST) {
                prefix = ':response ';
            } else {
                prefix = ':request ';
            }
            this._obuf = new OBuf(prefix + this.Id, this.connection.socket);
        }

        this._obuf.append(data, encoding);
        return;
    }
    get headersSent() {
        return !!this._header;
    }

    get _headers() {
        return this.getHeaders();
    }

    set _headers(val) {
        if (val === null) {
            this[outHeadersKey] = null;
        } else if (typeof val === 'object') {
            const headers = this[outHeadersKey] = {};
            const keys = Object.keys(val);
            let length = keys.length;
            for (let i = 0; i < length; ++i) {
                const name = keys[i];
                headers[name.toLowerCase()] = [name, val[name]];
            }
        }
    }

    get _headerNames() {
        const headers = this[outHeadersKey];
        if (headers) {
            const out = Object.create(null);
            const keys = Object.keys(headers);
            let length = keys.length;
            for (let i = 0; i < length; ++i) {
                const key = keys[i];
                const val = headers[key][0];
                out[key] = val;
            }
            return out;
        } else {
            return headers;
        }
    }

    set _headerNames(val) {
        if (typeof val === 'object' && val !== null) {
            const headers = this[outHeadersKey];
            if (!headers)
                return;
            const keys = Object.keys(val);
            let length = keys.length;
            for (let i = 0; i < length; ++i) {
                const header = headers[keys[i]];
                if (header)
                    header[0] = val[keys[i]];
            }
        }
    }
}

function onFinish(outmsg) {
    outmsg.emit('finish');
}

function escapeHeaderValue(value) {
    // Protect against response splitting. The regex test is there to
    // minimize the performance impact in the common case.
    return /[\r\n]/.test(value) ? value.replace(/[\r\n]+[ \t]*/g, '') : value;
}

function storeHeader(self, state, key, value, validate) {
    if (validate) {
        if (typeof key !== 'string' || !key || !checkIsHttpToken(key)) {
            throw new HttpWsError.TypeError(
                'ERR_INVALID_HTTP_TOKEN', 'Header name', key);
        }
        if (value === undefined) {
            throw new HttpWsError.TypeError('ERR_MISSING_ARGS', `header "${key}"`);
        } else if (checkInvalidHeaderChar(value)) {
            debug('Header "%s" contains invalid characters', key);
            throw new HttpWsError.TypeError('ERR_INVALID_CHAR', 'header content', key);
        }
    }
    state.header += key + ': ' + escapeHeaderValue(value) + CRLF;
    matchHeader(self, state, key, value);
}

function matchConnValue(self, state, value) {
    var sawClose = false;
    var m = RE_CONN_VALUES.exec(value);
    while (m) {
        if (m[0].length === 5)
            sawClose = true;
        else
            state.connUpgrade = true;
        m = RE_CONN_VALUES.exec(value);
    }
    if (sawClose)
        self._last = true;
    else
        self.shouldKeepAlive = true;
}

function matchHeader(self, state, field, value) {
    var m = RE_FIELDS.exec(field);
    if (!m)
        return;
    var len = m[0].length;
    if (len === 10) {
        state.connection = true;
        matchConnValue(self, state, value);
    } else if (len === 17) {
        state.te = true;
        if (RE_TE_CHUNKED.test(value)) self.chunkedEncoding = true;
    } else if (len === 14) {
        state.contLen = true;
    } else if (len === 4) {
        state.date = true;
    } else if (len === 6) {
        state.expect = true;
    } else if (len === 7) {
        var ch = m[0].charCodeAt(0);
        if (ch === 85 || ch === 117)
            state.upgrade = true;
        else
            state.trailer = true;
    }
}

function validateHeader(msg, name, value) {
    if (typeof name !== 'string' || !name || !checkIsHttpToken(name))
        throw new HttpWsError.TypeError('ERR_INVALID_HTTP_TOKEN', 'Header name', name);
    if (value === undefined)
        throw new HttpWsError.TypeError('ERR_MISSING_ARGS', 'value');
    if (msg._header)
        throw new HttpWsError.Error('ERR_HTTP_HEADERS_SENT', 'set');
    if (checkInvalidHeaderChar(value)) {
        debug('Header "%s" contains invalid characters', name);
        throw new HttpWsError.TypeError('ERR_INVALID_CHAR', 'header content', name);
    }
}

function write_(msg, chunk, encoding, callback, fromEnd) {
    if (msg.finished) {
//    var err = new Error('write after end');
//    nextTick(msg.socket && msg.socket[async_id_symbol],
//             writeAfterEndNT.bind(msg),
//             err,
//             callback);//

        return true;
    }

    if (!msg._header) {
        msg._implicitHeader();
    }

    if (!msg._hasBody) {
        debug('This type of response MUST NOT have a body. ' +
            'Ignoring write() calls.');
        return true;
    }

    if (!fromEnd && typeof chunk !== 'string' && !(chunk instanceof Buffer)) {
        throw new HttpWsError.TypeError('ERR_INVALID_ARG_TYPE', 'first argument',
            ['string', 'buffer']);
    }


    // If we get an empty string or buffer, then just do nothing, and
    // signal the user to keep writing.
    if (chunk.length === 0) return true;

//  if (!fromEnd && msg.connection && !msg.connection.corked) {
//    msg.connection.cork();
//    process.nextTick(connectionCorkNT, msg.connection);
//  }

    var len, ret;
    /*
      if (msg.chunkedEncoding) {
        if (typeof chunk === 'string')
          len = Buffer.byteLength(chunk, encoding);
        else
          len = chunk.length;

        msg._send(len.toString(16), 'latin1', null);
        msg._send(crlf_buf, null, null);
        msg._send(chunk, encoding, null);
        ret = msg._send(crlf_buf, null, callback);
      } else {
        ret = msg._send(chunk, encoding, callback);
      }
    */
    ret = msg._send(chunk, encoding, callback);

    debug('write ret = ' + ret);
    return ret;
}


function writeAfterEndNT(err, callback) {
    this.emit('error', err);
    if (callback) callback(err);
}



