import Command from './command'
import ServerResponse from './service_response'
import {HTTPParser} from './http-parser'

const continueExpression =  /(?:^|\W)100-continue(?:$|\W)/i;

export default class ServiceCommand extends Command {
    constructor(connection, id){
        super(connection, id, HTTPParser.REQUEST);
        let n = this.app.maxHeadersCount;
        // Propagate headers limit from server instance to parser
        if ( n && typeof n === 'number') {
            this._parser.maxHeaderPairs = n << 1;
        } else {
            // Set default value because parser may be reused from FreeList
            this._parser.maxHeaderPairs = 2000;
        }
        this._id = id;
        var self = this;
        this._timeout_handler = setTimeout(function () {
            self.emit('timeout_notify');
        }, 2 * 60 * 1000);
    }

    // The following callback is issued after the headers have been read on a
    // new message. In this callback we setup the response object and pass it
    // to the user.
    _parserOnIncoming( req, keepAlive) {


        let res = new ServerResponse(this, req);
        let conn = this.connection;
        let cmd  = this;
        let id = this._id;

        res.on('close',function(){
            clearTimeout(cmd._timeout_handler);
            cmd.emit('finish', id);
        });

        res.on('write',function( data, encoding, callback){
            cmd.write( data , encoding );
            if(callback){
                callback();
            }
        });

        //res._onPendingData = updateOutgoingData.bind(undefined, socket, state);

        //res.shouldKeepAlive = keepAlive;

        // When we're finished writing the response, check if this is the last
        // response, if so destroy the socket.
        //res.on('finish',
        //    resOnFinish.bind(undefined, req, res, socket, state, server));

        if (req.headers.expect !== undefined &&
            (req.httpVersionMajor === 1 && req.httpVersionMinor === 1)) {
            if (continueExpression.test(req.headers.expect)) {
                res._expect_continue = true;
                let app = this.app;

                if (app.listenerCount('checkContinue') > 0) {
                    app.emit('checkContinue', req, res);
                } else {
                    res.writeContinue();
                    app.emit('request', req, res);
                }
            } else {
                if (app.listenerCount('checkExpectation') > 0) {
                    app.emit('checkExpectation', req, res);
                } else {
                    res.writeHead(417);
                    res.end();
                }
            }
        } else {
            this.app.emit('request', req, res);
        }
        return false; // Not a HEAD response. (Not even a response!)
    }

}



